let engines = {
    google: {url: 'https://www.google.com/search?q=', icon: 'fa-google'},
    baidu: {url: 'https://www.baidu.com/s?wd=', icon: 'fa-paw'},
    bing: {url: 'https://cn.bing.com/search?q=', icon: 'fa-windows'},
    wiki: {url: 'https://zh.wikipedia.org/wiki/', icon: 'fa-wikipedia-w'},
    youdao: {url: 'http://youdao.com/w/', icon: 'fa-language'},
    github: {url: 'https://github.com/search?q=', icon: 'fa-github'},
    linux: {url: 'http://linux.51yip.com/search/', icon: 'fa-linux'},
    image: {url: 'https://www.picsearch.com/index.cgi?q=', icon: 'fa-photo'},
    music: {url: 'https://www.tikitiki.cn/search.do?page=1&type=2&keyword=', icon: 'fa-music'},
    video: {url: 'https://so.youku.com/search_video/', icon: 'fa-youtube-play'},
    doc: {url: 'https://www.docin.com/search.do?nkey=', icon: 'fa-file-text-o'},
    icon: {url: 'https://www.iconfinder.com/search/?price=free&q=', icon: 'fa-info-circle'},
    pan: {url: 'http://pansou.com/?q=', icon: 'fa-cloud-download'},
    // stock: ''
}

function getEngine(e, q) {
    let engine = e
    let sep
    if (q.includes(':')) {
        sep = ':'
    } else if (q.includes('：')) {
        sep = '：'
    } else {
        return {engine, q}
    }

    let key = q.split(sep)[0]
    if (key in engines) {
        engine = engines[key].url
        q = q.replace(`${key}${sep}`, '').trim()
    }
    return {engine, q}

}

export {
    engines,
    getEngine
}
