<template>
    <div class="app-page">
        <grid
            :draggable="true"
            :sortable="true"
            :items="appList"
            :gridWidth="gridWidth"
            :cellWidth="150"
            :cellHeight="150"
            @gridclick="goto($event)"
            @drag="dragStart"
            @dragend="dragEnd($event)">
            <template slot="cell" slot-scope="props">
                <div class="app-item">
                    <div class="pic" :style="getIcon(props.item)"></div>
                    <span class="title" v-text="props.item.title"></span>
                </div>
            </template>
        </grid>
        <transition name="slide">
            <trash v-show="trashVisible"/>
        </transition>
        <v-dialog/>
    </div>
</template>

<script>
import { apps } from "../assets/js/apps";
import Trash from "./Trash.vue";
import Grid from "./grid/Grid.vue";

export default {
    name: "Application",
    components: {
        Grid,
        Trash
    },
    data() {
        return {
            // apps,
            appList: [],
            trashVisible: false
        };
    },
    computed: {
        gridWidth: function() {
            return document.body.clientWidth * 0.8;
        }
    },
    beforeCreate() {
        this.$vlf.getItem("appList").then(value => {
            if (value) {
                this.appList = value;
            } else {
                this.appList = apps;
            }
        });
    },
    mounted() {
        document.addEventListener("mouseup", this.isNeedToDelete);
    },
    methods: {
        getIcon(i) {
            return {
                backgroundImage: `url(${i.icon})`
            };
        },
        goto(e) {
            if (e.items[e.sort].item.href) {
                window.open(e.items[e.sort].item.href);
            }
        },
        dragEnd(e) {
            let items = e.items.map(i => i.item);
            this.$vlf.setItem("appList", items);
            this.trashVisible = false;
        },
        dragStart() {
            this.trashVisible = true;
        },
        isNeedToDelete(e) {
            let r = document.querySelector("#trash").getBoundingClientRect();
            if (
                e.x > r.left &&
                e.x < r.right &&
                e.y > r.top &&
                e.y < r.bottom
            ) {
                this.$modal.show("dialog", {
                    title: "提示",
                    text: "确定要删除这个应用吗？",
                    buttons: [
                        {
                            title: "取消"
                        },
                        {
                            title: "确定",
                            handler: () => {
                                // console.log('you have delete me');
                                this.$modal.hide('dialog')
                            }
                        }
                    ]
                });
            }
        },
    }
};
</script>

<style scoped lang="stylus">
.slide-enter-active, .slide-leave-active {
    transition: bottom 0.3s ease;
}

.slide-enter, .slide-leave-to {
    bottom: -230px;
}

.app-page {
    height: 80vh;
    width: 80vw;
    position: absolute;
    top: 10vh;
    left: 10vw;
    overflow: visible;
    user-select: none;

    .app-item {
        width: 150px;
        height: 150px;
        cursor: pointer;

        .pic {
            width: 100%;
            height: calc(100% - 50px);
            background-size: 80px 80px;
            background-repeat: no-repeat;
            background-position: center center;
        }

        .title {
            display: block;
            width: 100%;
            height: 50px;
            font-size: 16px;
            font-weight: 500;
            line-height: 50px;
            text-align: center;
            overflow: hidden;
            text-overflow: ellipsis;
            white-space: nowrap;
            color: #fff;
            text-shadow: 1px 1px 3px #00000080;
        }

        &:hover {
            background-color: #ffffff20;

            .title {
                text-shadow: 1px 1px 3px #00000000;
            }
        }
    }
}
</style>
